import { player1, player2 } from './pokemonscreate.js'
import {currentCountHits} from './main.js'

const $logs = document.getElementById('logs')

function generateLog(whoDamaged, whoHit, count) {
  const { name, hp: { current, total} } = whoDamaged;
  const { name: enemyName } = whoHit;

  const logs = [
    `${name} –≤—Å–ø–æ–º–Ω–∏–ª —á—Ç–æ-—Ç–æ –≤–∞–∂–Ω–æ–µ, –Ω–æ –Ω–µ–æ–∂–∏–¥–∞–Ω–Ω–æ ${enemyName}, –Ω–µ –ø–æ–º–Ω—è —Å–µ–±—è –æ—Ç –∏—Å–ø—É–≥–∞, —É–¥–∞—Ä–∏–ª –≤ –ø—Ä–µ–¥–ø–ª–µ—á—å–µ –≤—Ä–∞–≥–∞. - ${count}  hp. –£ ${name} [${current}/${total}]`,
    `${name} –ø–æ–ø–µ—Ä—Ö–Ω—É–ª—Å—è, –∏ –∑–∞ —ç—Ç–æ ${enemyName} —Å –∏—Å–ø—É–≥—É –ø—Ä–∏–ª–æ–∂–∏–ª –ø—Ä—è–º–æ–π —É–¥–∞—Ä –∫–æ–ª–µ–Ω–æ–º –≤ –ª–æ–± –≤—Ä–∞–≥–∞. - ${count} hp. –£ ${name} [${current}/${total}]`,
    `${name} –ø—Ä–∏—à–µ–ª –≤ —Å–µ–±—è, –Ω–æ –Ω–µ–æ–∂–∏–¥–∞–Ω–Ω–æ ${enemyName} —Å–ª—É—á–∞–π–Ω–æ –Ω–∞–Ω–µ—Å –º–æ—â–Ω–µ–π—à–∏–π —É–¥–∞—Ä. - ${count} hp. –£ ${name} [${current}/${total}]`,
    `${name} –ø–æ–ø–µ—Ä—Ö–Ω—É–ª—Å—è, –Ω–æ –≤ —ç—Ç–æ –≤—Ä–µ–º—è ${enemyName} –Ω–µ—Ö–æ—Ç—è —Ä–∞–∑–¥—Ä–æ–±–∏–ª –∫—É–ª–∞–∫–æ–º \<–≤—ã—Ä–µ–∑–∞–Ω–Ω–æ —Ü–µ–Ω–∑—É—Ä–æ–π\> –ø—Ä–æ—Ç–∏–≤–Ω–∏–∫–∞. - ${count} hp. –£ ${name} [${current}/${total}]`,
    `${name} —É–¥–∏–≤–∏–ª—Å—è, –∞ ${enemyName} –ø–æ—à–∞—Ç–Ω—É–≤—à–∏—Å—å –≤–ª–µ–ø–∏–ª –ø–æ–¥–ª—ã–π —É–¥–∞—Ä. - ${count} hp. –£ ${name} [${current}/${total}]`,
    `${name} –≤—ã—Å–º–æ—Ä–∫–∞–ª—Å—è, –Ω–æ –Ω–µ–æ–∂–∏–¥–∞–Ω–Ω–æ ${enemyName} –ø—Ä–æ–≤–µ–ª –¥—Ä–æ–±—è—â–∏–π —É–¥–∞—Ä. - ${count} hp. –£ ${name} [${current}/${total}]`,
    `${name} –ø–æ—à–∞—Ç–Ω—É–ª—Å—è, –∏ –≤–Ω–µ–∑–∞–ø–Ω–æ –Ω–∞–≥–ª—ã–π ${enemyName} –±–µ—Å–ø—Ä–∏—á–∏–Ω–Ω–æ —É–¥–∞—Ä–∏–ª –≤ –Ω–æ–≥—É –ø—Ä–æ—Ç–∏–≤–Ω–∏–∫–∞. - ${count} hp. –£ ${name} [${current}/${total}]`,
    `${name} —Ä–∞—Å—Å—Ç—Ä–æ–∏–ª—Å—è, –∫–∞–∫ –≤–¥—Ä—É–≥, –Ω–µ–æ–∂–∏–¥–∞–Ω–Ω–æ ${enemyName} —Å–ª—É—á–∞–π–Ω–æ –≤–ª–µ–ø–∏–ª —Å—Ç–æ–ø–æ–π –≤ –∂–∏–≤–æ—Ç —Å–æ–ø–µ—Ä–Ω–∏–∫–∞. - ${count} hp. –£ ${name} [${current}/${total}]`,
    `${name} –∑–∞–±—ã–ª—Å—è, –Ω–æ –≤ —ç—Ç–æ –≤—Ä–µ–º—è –Ω–∞–≥–ª—ã–π ${enemyName}, –ø—Ä–∏–Ω—è–≤ –≤–æ–ª–µ–≤–æ–µ —Ä–µ—à–µ–Ω–∏–µ, –Ω–µ—Å–ª—ã—à–Ω–æ –ø–æ–¥–æ–π–¥—è —Å–∑–∞–¥–∏, —É–¥–∞—Ä–∏–ª. - ${count} hp. –£ ${name} [${current}/${total}]`,
    `${name} –ø—ã—Ç–∞–ª—Å—è —á—Ç–æ-—Ç–æ —Å–∫–∞–∑–∞—Ç—å, –Ω–æ –≤–¥—Ä—É–≥, –Ω–µ–æ–∂–∏–¥–∞–Ω–Ω–æ ${enemyName} —Å–æ —Å–∫—É–∫–∏, —Ä–∞–∑–±–∏–ª –±—Ä–æ–≤—å —Å–æ–ø–µ—Ä–Ω–∏–∫—É. - ${count} hp. –£ ${name} [${current}/${total}]`
  ];

return logs[Math.floor(Math.random() * logs.length)];
}

function writeLog(whoDamaged, whoHit) {
  return function writeLog4Selectors(damage) {
    const log = generateLog(whoDamaged, whoHit, damage);
    const $p = document.createElement('p');
    $p.innerText =  currentCountHits() + "." + log;
    $logs.insertBefore($p, $logs.children[0]);
  }
}

export const writeLog4Player = writeLog(player1, player2)
export const writeLog4Enemy = writeLog(player2, player1)
